.tab {
    width: 100%;
    height: 30%;
    padding: .7rem;
    border-radius: 10px;
    margin-bottom: 1.2rem;
    display: flex;
    justify-content: space-between;
    background-color: #71d8f1;
}

.tab .details {
    flex: 1;
    display: flex;
    flex-direction: column;
}

.details .details-tab {
    display: flex;
    justify-content: flex-start;
}

.details-tab .address {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.details-tab .address p:first-child{
    margin-bottom: .5rem;
    text-transform: capitalize;
}

.details-tab .address p:last-child {
    font-size: .9rem;
}

.tab p.description {
    font-weight: 600;
    text-transform: capitalize;
    margin-bottom: 1rem;
}

.tab p.temp {
    font-size: 3.0rem;
}

.tab p.temp::after {
    content: "";
    border: 1px solid green;
    height: 10%;
    margin-right: .9rem;
}

.tab p.temp span {
    margin-left: -1.8rem;
}

.tab img {
    height: 100%;
    margin-right: 0;
    display: block;
}